@import "vars";

#c {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 5555;
}

#cc {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.lcdm {
  flex-grow: 1;
  align-items: center;
  display: flex;
  .column {
    background-color: rgba(white, 0.2);
    border-radius: 6px;
    margin: 10px;
    p {
      text-align: justify;
      color: #bfbfbf;
    }

    .title{
      color: $tonico;
    }
  }
}

#dyn-bg {
  --vanta-background-color: 0x0f0f0f;
  --vanta-color: 0x000000;
}

.cube{
  &.is-4, &.is-2, &.is-3{
    float: right;
    margin: 1em;
  }
  width: 10vw;
  max-width: 100px;
}

.container-puzzle{
  max-width: 50vw;
}

.puzzle{
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 25px 0;
}

.puzzle-logo{
  z-index: 33;
  position:relative;
}